*{
  
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Times New Roman', Times, serif;
}
.container{
  width: 350px;
  margin: 0 auto;
  margin-top: 50px;
  background-color:skyblue;
  border-radius: 20px;
}

.searchbox input{
  
  
  padding:5px 5px 5px 20px;
   border-radius: 20px; 
  
  border: none;
  margin-right: 5px;
  outline: none;
  
}

.searchbox i{
  
  font-weight: bold;
  
  /* font-size: 18px; */
  
  color:white;
}
.searchbox i:hover{
  color:black;
}
.icon{
  font-size: 50px;
}
.border{
  height: 2px;
  background-color: blueviolet;
  margin:10px 0px 10px 0px;
}